<template>
    <div>
        <el-table :data="posts" :show-header="false" style="width:100%;" class="postslist">

            <el-table-column align="right" class-name="symbol" width="55">
                <template scope="scope">
                    <el-popover trigger="hover" placement="top">
                        <p>分类: <a href="#"></a></p>
                        <!-- 添加说明 -->
                        <div slot="reference" class="name-wrapper">
                            <i><strong>=></strong></i>
                        </div>
                    </el-popover>
                </template>
            </el-table-column>

            <el-table-column width="600">
                <template scope="scope">
                    <a href="#" class="posttitle">{{ scope.row.title }}</a>
                </template>
            </el-table-column>
           
            <el-table-column align="right">
                <template scope="scope">
                    <el-button type="text">阅读</el-button>
                </template>
            </el-table-column>
            
        </el-table>
       
    </div>
</template>
<script>
import axios from 'axios';

export default {
    data(){
        return{
            posts:[],
            allPage:10,
            prePage:12,
            nowPage:1,
            loading:false,
        }
    },
    created(){
        this.getPosts()
    },
    methods:{
        getPosts(){
            axios.get('/api/posts').then((res) => {
                console.log(res)
                this.posts = res.data.items
            })
        },
        handleCurrentChange(val){
            this.nowPage = val
        },
    }
}
</script>

<style>
    .symbol{
        padding: 0px;
        margin: 0px;
        color: #ff6670;
    }
    .postslist{
        margin: 0px 15px;
    }
    .posttitle{
        text-decoration: none;
        color: #006699;
    }
</style>



